const championsInitialState = [
 {
    id: 0,
    name: 'Ashe',
    img: 'http://ddragon.leagueoflegends.com/cdn/img/champion/splash/Ashe_8.jpg',
    title: 'a Arqueira do Gelo',
    description: 'A cada flecha que dispara de seu arco ancestral encantado com gelo, Ashe prova que  uma arqueira-mestra. Ela escolhe cada alvo cuidadosamente, espera pelo momento certo e o atinge com poder e preciso.  com a mesma viso e foco que ela persegue o objetivo de unificar as tribos de Freljord e mold-las em uma poderosa nao.',
    isVisible: true,
  },
 {
    id: 1,
    name: 'Ahri',
    img: 'http://ddragon.leagueoflegends.com/cdn/img/champion/splash/Ahri_7.jpg',
    title: 'a Raposa de Nove Caudas',
    description: 'Ao contrrio de outras raposas que povoavam os bosques do sul de Ionia, Ahri sempre sentiu uma estranha conexo com o mundo mgico que existia  sua volta; uma ligao incompleta, de certa forma. Em seu corao ela sentia que estava vivendo em um corpo inadequado, e sonhava em um dia se tornar humana. Este objetivo parecia estar eternamente fora de alcance, at que ela se viu nos rastros de uma guerra humana.',
    isVisible: false,
  },
 {
    id: 2,
    name: 'Twisted Fate',
    img: 'http://ddragon.leagueoflegends.com/cdn/img/champion/splash/TwistedFate_5.jpg',
    title: 'o Mestre das Cartas',
    description: 'Twisted Fate, o Mestre das Cartas No  apostar se voc no puder perder... Exmio com cartas e vigarista astuto, o infame Twisted Fate apostou e seduziu seu caminho por grande parte do mundo conhecido, recebendo a inimizade e admirao dos ricos e tolos. Ele raramente leva as coisas a srio, recebendo cada dia com um sorriso debochado e uma lisonja impassvel. De todas as maneiras possveis, Twisted Fate sempre tem um s na manga.',
    isVisible: false,
  },
 {
    id: 3,
    name: 'Lee Sin',
    img: 'http://ddragon.leagueoflegends.com/cdn/img/champion/splash/LeeSin_2.jpg',
    title: 'o Monge Cego',
    description: 'Quando ainda era um jovem adolescente, Lee Sin tinha a inteno de se tornar um invocador. Sua vontade e dedicao eram incomparveis, e sua habilidade chamou a ateno de Reginald Ashram, o Alto Conselheiro da Liga na poca. Enquanto era estudante na Arcanum Majoris, Lee Sin ficou frustrado com o nvel de instruo, adequado ao ritmo dos demais estudantes. Ele passava seu tempo livre pesquisando as nuances da invocao na esperana de se formar mais cedo. Fez incrveis avanos em seus estudos arcanos, ultrapassando todos os outros alunos.',
    isVisible: false,
  },
  {
    id: 4,
    name: 'Ezreal',
    img: 'http://ddragon.leagueoflegends.com/cdn/img/champion/splash/Ezreal_0.jpg',
    title: 'o Explorador Prdigo',
    description: 'O intrpido jovem aventureiro Ezreal explorou parte das mais remotas e abandonadas regies de Runeterra. Durante uma expedio s runas soterradas da antiga Shurima, ele recuperou um amuleto de incrvel poder mstico. Possivelmente construdo para ser usado por um dos Ascendentes, o gigantesco talism serviu confortavelmente em seu brao, amplificando suas habilidades mgicas brutas a tal ponto que lhe rendeu a reputao de um heri, para muito de seu constrangimento.',
    isVisible: false,
  },
 {
    id: 5,
    name: 'Olaf',
    img: 'http://ddragon.leagueoflegends.com/cdn/img/champion/splash/Olaf_0.jpg',
    title: 'o Berserker',
    description: 'A maioria dos homens diria que a morte  algo a ser temido; nenhum desses homens seria Olaf. O Berserker vive somente pelo bramido de um grito de guerra e da coliso das espadas. Impelido por sua fome de glria e pela maldio iminente de uma morte esquecvel, Olaf se mete em qualquer briga com abandono negligente. Rendendo-se  sede de sangue que existe em si, ele somente est vivo de verdade quando agarra a morte pelas presas.',
    isVisible: false,
  },

]
export default (state = championsInitialState, action) => {
  switch (action.type) {
    case 'FETCH_ALL_CHAMPS':
      return state;
    case 'FETCH_CHAMP_DETAILS':
      return state === action.payload;
    case 'SET_CHAMP_VISIBILITY':
      const { id, isVisible } = action.payload;
      const champions = [...state];
      const championToUpdate = champions.findIndex(champion => champion.id === id);
      champions[championToUpdate].isVisible = isVisible;
      return champions;
    default:
      return state;
  }
}



